#hero {
  background-repeat: no-repeat;
  background-color: $product-block-color-first;
  height: 502px;

  // .ice {
  //   position: absolute;
  //   left: 117px;
  //   bottom: 0%;
  // }

  .hero-left {
    position: absolute;
    z-index: 2;
  }

  .hero-text {
    height: 502px;
    font-family: 'Titan One';
    padding-top: 193px;

    h1 {
      font-weight: 400;
      font-size: 26px;
      line-height: 1.15;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $main-color;
    }

    .heading {
      font-size: 48px;
      line-height: 1;
      color: $accent-color;
    }

    button {
      margin-top: 19px;
      font-family: 'DM Sans';
      font-weight: 400;
      font-size: 8px;
      line-height: 1.25;
      border-radius: 20px;
    }

    .btn-product {
      margin-right: 8px;
      background-color: $accent-color;
      color: $main-color;
      padding: 9px 15px 9px 15px;
      transition: background-color $time-function;
      cursor: pointer;
    }

    .btn-made {
      background-color: $main-color;
      color: $accent-color;
      padding: 7px 13px 7px 13px;
      transition: background-color $time-function;
      cursor: pointer;
    }

    .btn-product:hover,
    .btn-product:focus {
      background: $main-btn-color-focus;
    }

    .btn-made:hover,
    .btn-made:focus {
      background: $mark-color;
    }


    .hero-left {
      position: absolute;
      z-index: 2;
      width: 218px;

    }


  }


}

#hero {
  @media screen and(max-width:459px) {

    @media screen and (min-resolution: 2dppx) {
      .hero {
        background-image: url(/images/mobile/hero/ellipse4@2x.png);
        background-position: bottom 80px right -50px;
      }
    }

    @media screen and (min-resolution: 3dppx) {
      .hero {
        background-image: url(/images/mobile/hero/ellipse4@3x.png);
        background-position: bottom 80px right -50px;
      }
    }


    background-image: url(/images/mobile/hero/ellipse4.png);
    background-position: bottom 80px right -50px;




    @media screen and (min-resolution: 2dppx) {
      .hero-discription {
        background-image: url(/images/mobile/hero/ice-cream-171@2x.png);

      }
    }

    @media screen and (min-resolution: 3dppx) {
      .hero-discription {
        background-image: url(/images/mobile/hero/ice-cream-171@3x.png);

      }
    }

    .hero-discription {
      position: relative;
      background-image: url(/images/mobile/hero/ice-cream-171@1x.png);
      background-position: bottom 0 right 50px;
      background-repeat: no-repeat;
    }

    .ice {
      display: none;
    }
  }
}

#hero {
  @media screen and(min-width:460px) {
    .hero-discription {
      position: relative;
    }

    .hero-circle {
      position: absolute;
      top: 75px;
      left: 36px;
      background-color: $accent-pink-color;
      border-radius: 50%;
      height: 344px;
      width: 344px;
    }

    .ice {
      position: absolute;
      left: 117px;
      bottom: 0;
    }
  }
}

// placeholder
.number-circle1 {
  @extend %num;
  right: 0%;
  top: 24%;
}

.number-circle2 {
  @extend %num;
  right: 0%;
  top: 38%;
}

#hero {
  @media screen and (max-width: 767px) {

    .hero-right,
    .hero-bottom {
      display: none;
    }

    .hero-left {
      left: 20px;
    }
  }

  @media screen and (min-width: $tablet) {
    height: 400px;

    .hero-text {
      position: relative;
      display: flex;
      height: 400px;
      padding-top: 100px;

      h1 {
        font-size: 22px;
        line-height: 1.14;
        letter-spacing: 0.04em;
      }

      .heading {
        font-size: 34px;
        line-height: 1.1;
      }
    }

    .hero-circle {
      position: absolute;
      top: 31px;
      left: 307px;

      background-color: $accent-pink-color;
      border-radius: 50%;
      height: 324px;
      width: 324px;
    }

    .ice {
      position: absolute;
      left: 378px;
      bottom: 0%;
    }


    .hero-right {
      margin-left: 648px;
      text-align: right;

      .number:first-child {
        margin-bottom: 13px;
      }

      .number p {
        font-size: 28px;
        line-height: 1.14;
        color: $accent-color;
      }

      .number p span {
        display: block;
        font-family: 'DM Sans';
        font-size: 8px;
        line-height: 1.25;
        color: $main-color;
      }

      .number-circle1,
      .number-circle2 {
        width: 16px;
        height: 16px;
      }

      .number-link1,
      .number-link2 {
        position: relative;
      }
    }

    .img-tasty {
      position: absolute;
      bottom: 0%;
      right: 0;
    }

    .img-carafe {
      position: absolute;
      bottom: 20px;
      left: 0;
      display: flex;
    }

    .img-carafe li:last-child {
      margin-top: 47px;
    }

    .lorem {
      width: 114px;
      font-family: 'DM Sans';
      font-size: 8px;
      line-height: 1.25;
      color: $main-color;
      margin-bottom: 7px;
    }

    .link {
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background-color: $main-color;

      transition: background-color $time-function;
      cursor: pointer;

      svg {
        width: 7px;
      }
    }
  }

  .link:hover,
  .link:focus {
    background-color: $secondary-btn-color-focus;
  }

  @media screen and (min-width: $desktop) {
    height: 665px;

    .hero-circle {
      position: absolute;
      top: 52px;
      left: 360px;

      background-color: $accent-pink-color;
      border-radius: 50%;
      height: 538px;
      width: 538px;
    }

    .ice {
      position: absolute;
      left: 479px;
      bottom: 0%;
    }

    .hero-text {
      height: 665px;
      padding-top: 171px;

      button {
        margin-top: 25px;
        font-size: 14px;
        line-height: 1.28;
      }

      .btn-product {
        margin-right: 15px;
        padding: 11px 24px 11px 24px;
      }

      .btn-made {
        padding: 11px 20px 11px 20px;
      }

      .hero-left {
        width: 277px;
      }

      .hero-right {
        margin-left: 957px;
      }

      h1 {
        font-size: 38px;
        line-height: 1.16;
        letter-spacing: 0.04em;
      }

      .heading {
        font-size: 58px;
        line-height: 0.89;
      }

      .hero-right {
        .number-circle1 {
          top: 25%;

          width: 30px;
          height: 30px;
        }

        .number-circle2 {
          top: 38%;

          width: 30px;
          height: 30px;
        }

        .number p {
          font-size: 46px;
          line-height: 1.15;
          color: $accent-color;
        }

        .number span {
          font-size: 14px;
          line-height: 1.28;
          color: $main-color;
        }
      }

      .img-carafe {
        position: absolute;
        bottom: 40px;
        left: 0;
        display: flex;
      }

      .img-carafe li:last-child {
        margin-top: 56px;
      }

      .lorem {
        width: 200px;
        font-size: 14px;
        line-height: 1.43;
        margin-bottom: 20px;
      }

      .link {
        width: 26px;
        height: 26px;

        svg {
          width: 7px;
        }
      }
    }
  }
}